@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: none;
  clip: rect(0 0 0 0);
}

.page {
  height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

.page__wrapper {
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.button {
  display: block;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
.button:focus-visible {
  outline: 2px #81b3d2 solid;
  outline-offset: 2px;
}
.button:disabled {
  background-color: #e5e5e5;
}

.main-header {
  position: relative;
  display: flex;
  min-height: 56px;
}

.main-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;
  display: block;
  width: 185px;
  height: 96px;
  transform: translate(-50%);
  cursor: pointer;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.6;
}
.main-header__logo:focus-visible {
  outline: 2px #81b3d2 solid;
  outline-offset: 2px;
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 3;
  }
}

.main-header__toggle {
  margin-top: 17px;
  margin-right: 20px;
  margin-left: auto;
}

.main-nav {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: none;
  width: 100%;
  min-height: 56px;
  background-color: #81b3d2;
}
.is-open .main-nav {
  display: flex;
}
.no-js .main-nav {
  position: relative;
  display: flex;
}
@media (min-width: 768px) {
  .main-nav {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0);
  }
}

.main-nav__list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}

.main-nav__item {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.main-nav__item:last-child {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__item {
    display: flex;
    justify-content: center;
    box-shadow: none;
    padding: 5px 10px;
  }
  .main-nav__item:last-child {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0));
  }
  .main-nav__item:nth-child(4n-1) {
    grid-column-start: 4;
  }
}

.main-nav__link {
  display: block;
  padding: 17px 77px 12px 77px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}
.main-nav__link:hover {
  background-color: #669ec0;
}
.main-nav__link:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
@media (min-width: 768px) {
  .main-nav__link {
    padding: 0;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    box-shadow: none;
  }
  .main-nav__link--current {
    color: #766357;
  }
  .main-nav__link:hover {
    color: #81b3d2;
    background-color: rgba(0, 0, 0, 0);
  }
  .main-nav__link:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: rgba(0, 0, 0, 0);
  }
  .main-nav__link:focus-visible {
    outline: 2px #81b3d2 solid;
    outline-offset: 2px;
  }
}

.burger-button {
  position: relative;
  z-index: 3;
  display: block;
  width: 23px;
  height: 22px;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  cursor: pointer;
}
.burger-button::before, .burger-button::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #81b3d2;
  transition: all 0.3s ease 0s;
}
.burger-button::before {
  top: 0;
}
.burger-button::after {
  bottom: 0;
}
.no-js .burger-button {
  display: none;
}
.burger-button:hover {
  opacity: 0.6;
}
.burger-button:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .burger-button {
    display: none;
  }
}

.burger-button__icon {
  position: absolute;
  top: 9px;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #81b3d2;
  transition: all 0.3s ease 0s;
}

.is-open .burger-button::before {
  top: 9px;
  background-color: #ffffff;
  transform: rotate(45deg);
}

.is-open .burger-button::after {
  bottom: 9px;
  background-color: #ffffff;
  transform: rotate(-45deg);
}

.is-open .burger-button__icon {
  height: 0;
}

